//         , lightbox  API
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';
import {
  getImages,
  setSearchQuery,
  resetPage,
  nextPage,
  updateFirstSearch,
} from './js/apiService';
import { createMarkup } from './js/markup';
import { Notify } from 'notiflix';
import { Report } from 'notiflix/build/notiflix-report-aio';

//     DOM
const form = document.querySelector('#search-form');
const gallery = document.querySelector('.gallery');
const loadMoreBtn = document.querySelector('.load-more');

//    "Load more"
loadMoreBtn.style.display = 'none';

//     
form.addEventListener('submit', onSubmit);

// ,     
function onSubmit(evt) {
  evt.preventDefault();

  // ,     
  if (evt.target.searchQuery.value === '') {
    Notify.warning('Input field is empty');
    return;
  }

  //    
  setSearchQuery(evt.target.searchQuery.value);

  //     1
  resetPage();

  //    
  updateFirstSearch(true);

  //   "Load more"    
  loadMoreBtn.hidden = true;

  //    API
  getImages().then(data => {
    //     
    gallery.insertAdjacentHTML('beforeend', createMarkup(data));

    //  lightbox
    new SimpleLightbox('.gallery a', {
      captionDelay: 200,
      captionsData: 'alt',
    });

    //   "Load more"
    loadMoreBtn.hidden = false;
    loadMoreBtn.style.display = 'block';
  });

  //   
  evt.target.searchQuery.value = '';

  //  
  gallery.innerHTML = '';
}

//      "Load more"
loadMoreBtn.addEventListener('click', onLoadMore);

// ,      "Load more"
function onLoadMore() {
  //     
  nextPage().then(data => {
    if (!data.length) {
      Report.info(
        "We're sorry",
        "but you've reached the end of search results.",
        'Okay'
      );
      loadMoreBtn.hidden = true;
    } else {
      renderGallery(data);
    }
  });
}

//       
function renderGallery(data) {
  //     
  gallery.insertAdjacentHTML('beforeend', createMarkup(data));

  //  lightbox   
  new SimpleLightbox('.gallery a', {
    captionDelay: 200,
    captionsData: 'alt',
  });
}

//   
window.onload = function () {
  // ,    
  if (performance.getEntriesByType('navigation')[0].type === 'reload') {
    //    , ,   URL  localStorage
    if (localStorage.getItem('previousPage')) {
      //   URL  localStorage,     
      window.location.href = localStorage.getItem('previousPage');
    }
  }
};
